* {
    margin: 0;
    padding: 0;
    font-family: Georgia, 'Times New Roman', Times, serif;
    box-sizing: border-box;
}

body {
    background-color: #000;
}

.container {
    // max-width: 1350px;
    width: 100%;
    padding: 0 10px;
    margin: 0 auto;
    display: flex;
}

