.footer {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    background-color: rgba(124, 123, 123, 0.719);
    box-shadow: 0 0 40px rgba(0, 0, 0, .2) inset;
}

.footer-container {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    a {
        color: rgb(32, 32, 32);
        margin: 1px 10px;
    }
    a:hover {
        text-decoration: underline;
    }
    .fab {
        font-size: 20px;
    }
}

.footer-title {
    font-size: 16px;
    margin-right: 15px;
}

.footer-link {
    display: flex;
    justify-content: center;
    align-items: center;
}

.footerLogo {
    width: 60px;
}